body {
  margin: 0;
  padding: 0;
  background-image: url("img/body.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% 100%;
}

.App {
  display: flex wrap;
  flex-flow: column nowrap;
  width: 80%;
  margin: 0 auto;
  align-items: flex-start;
  padding: 0.5% 8%;
  background-color: rgba(0, 0, 0, 0.5);
}

.header-container {
  margin-top: 50px;
  margin-bottom: 50px;
  background-color: rgba(0, 0, 0, 0.5);
}

h1 {
  position: relative;
  padding-top: 40px;
  margin: 0;
  font-family: "Lobster", cursive;
  font-size: 60px;
  font-weight: 400;
  color: white;
  text-align: center;
  text-transform: uppercase;
  padding-bottom: 5px;
}

h1:before {
  width: 35px;
  height: 5px;
  display: block;
  content: "";
  position: absolute;
  bottom: 3px;
  left: 50%;
  margin-left: -14px;
  background-color: white;
}

h1:after {
  width: 150px;
  height: 1px;
  display: block;
  content: "";
  position: relative;
  margin-top: 25px;
  left: 50%;
  margin-left: -75px;
  background-color: white;
}

/* hotels list */
.hotels-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.hotel {
  padding: 10px;
  margin: 16px 10px;
  display: flex;
  position: relative;
  cursor: pointer;
  border-radius: 3px;
  background-color: white;
}
.hotel:hover {
  background-color: lightblue;
}
.hotel__image {
  display: block;
  width: 275px;
  height: 200px;
  margin-right: 20px;
}
.hotel__text {
  width: 100%;
  color: #888;
}
.hotel__header {
  display: flex;
  justify-content: space-between;
}
.hotel__name {
  font-size: 20px;
  padding: 3px 0;
  color: #07c;
  font-weight: bold;
  margin: 0;
  line-height: 28px;
}
.hotel__rating {
  display: block;
  color: white;
  background: darkblue;
  font-size: 16px;
  font-weight: bolder;
  padding: 10px;
  border-radius: 10px 10px 10px 0;
}
.hotel__main {
  display: flex;
  justify-content: space-between;
  height: 160px;
}
.hotel__perks {
  display: block;
  color: green;
}
.hotel__rooms-remaining {
  display: block;
  color: red;
  margin-top: 20px;
}
.hotel__main--right {
  display: flex;
  flex-direction: column;
  text-align: right;
  margin-top: auto;
}
.hotel__price {
  color: green;
  font-size: 20px;
}
.hotel__see-rooms {
  padding: 10px;
  background-color: darkblue;
  color: white;
  font-weight: bolder;
  cursor: pointer;
  margin-top: 5px;
}
.hotel__see-rooms:hover {
  box-shadow: 0 0 1px 1px #333;
}
.hotel__best-seller-flag {
  position: absolute;
  top: -8px;
  left: -8px;
  padding: 10px 15px;
  background-color: green;
  color: white;
  border-radius: 0 5px 5px 0;
  box-shadow: 1px 1px 2px black;
}
.hotel__best-seller-flag::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -8px;
  width: 0;
  height: 0;
  border: 4px solid darkgreen;
  border-bottom-color: transparent;
  border-left-color: transparent;
}

/* filters list */
.filters {
  width: 80%;
  border-radius: 5px;
  padding: 10px;
  margin: 16px 5px;
  margin-left: 8%;
  margin-bottom: 40px;
  background-color: white;
}
.filters__header {
  margin: 0;
  padding: 8px 5px;
  font-size: 16px;
}
.filters-list {
  margin: 0;
  padding: 0;
}
.filter {
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
}
.filter__checkbox {
  margin-right: 10px;
}

.filter__match-count {
  color: #888;
}

@media only screen and (max-width: 600px) {
  .App {
    width: 90%;
  }

  .hotel {
    display: flex;
    flex-flow: column nowrap;
    padding: 20px;
    margin: 15px 0px;
  }
}
